export default {
	//首页列表时间转化
	dateTime(e) {
		let old = new Date(e)
		let now = new Date()
		//获取old时间
		let d = old.getTime()
		let h = old.getHours()
		let m = old.getMinutes()
		let Y = old.getYear()
		let M = old.getMonth()
		let D = old.getDate()
		//获取now时间
		let nd = now.getTime()
		let nh = now.getHours()
		let nm = now.getMinutes()
		let nY = now.getYear()
		let nM = now.getMonth()
		let nD = now.getDate()
	
	  //当天时间
		if(Y === nY && M ===nM && D === nD) {
			if(h<10) {
				h = '0' + h
			}
			if(m<10) {
				m = '0' + m
			}
			return h+':'+m
		}
		
		//前天时间
		if(Y === nY && M ===nM && D+1 === nD) {
			if(h<10) {
				h = '0' + h
			}
			if(m<10) {
				m = '0' + m
			}
			return '前天'+h+':'+m
		} else {
			//大于两天
			return Y+'/'+M+'/'+D
		}
	}
}